import moment from 'moment';

function formatTime(date) {
	var year = date.getFullYear();
	var month = date.getMonth() + 1;
	var day = date.getDate();

	var hour = date.getHours();
	var minute = date.getMinutes();
	var second = date.getSeconds();

	return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':');
}

function formatNumber(n) {
	n = n.toString();
	return n[1] ? n : '0' + n;
}
/**
 * request
 */
function request(url, data = {}, method = 'GET', errCallBack = '') {
	





































































	

	let organize = uni.getStorageSync('locationInfo')
	let uuid = organize ? organize['locationSelect']['value'] : null
	return new Promise(function(resolve, reject) {
			uni.request({
				url: url,
				data: data,
				method: method,
				header: {
					'Content-Type': 'application/json',
					'X-Token': uni.getStorageSync('thirdInformation')['userToken'],
					'cookie': 'nervsid=7f99ffe2df0246cfa2c6ec30fb83c08e',
					'organize': uuid
				},
				success: function(res) {
					if (res.statusCode == 200) {
						if (res.data.success) {
							resolve(res.data);
			
						}
					} else if (res.statusCode == 403) {
						reject(res)
			
						uni.showToast({
							title: '',
							icon: 'none'
						});
						errCallBack && errCallBack();
			
					} else {
						reject(res)
						// uni.showToast({
						//                 title: '',
						//                 icon:'none'
						// 	});
						errCallBack && errCallBack();
					}
				},
				fail: function(err) {
					reject(err);
					console.log(err)
					uni.showToast({
						title: '',
						icon: 'none'
					});
					errCallBack && errCallBack();
				},
			});
	});

}
function wstLogin(wstCode){
	localStorage.setItem("thirdLogin",true);
	localStorage.setItem("wstCode",wstCode);
	localStorage.removeItem("userToken")
	let loginData={
		code:wstCode,
		thirdType:8
	};
	let BASEURL="https://"+getApp().globalData.wstBaseUrl;
	let urlRegister = BASEURL + "/app/api/objs/Parking/thirdLoginRegister";
	request(urlRegister,loginData, 'POST')
		.then(res => {
			if(res.data.requestThirdType){
				if(res.data.requestThirdType == -2){
					return;
				}
			}
			else{
				localStorage.setItem("loginCount",0);
				localStorage.setItem("thirdLogin",false);
				localStorage.setItem("userToken",res.data.userToken);
				let urlUser = BASEURL + '/app/api/objs/CurrentUser';
				request(urlUser, {}, 'GET').then(res => {
					localStorage.setItem("hasLogin",true);
					localStorage.setItem("userRegisterTime",res.data.registerTime);
					localStorage.setItem("userTel",res.data.accountTel);
				})
			}
	})
	.catch(err => {
		localStorage.setItem("loginCount",0);
		localStorage.setItem("thirdLogin",false);
		if(err.data.code == "idass.ThirdLogin.ThirdWstTokenError"){
			uni.showModal({
				title: '',
				content: '',
				showCancel: false,
			})
		}
		else if(err.data.code == "SDK.ReadTimeout"){
			uni.showModal({
				title: '',
				content: '',
				showCancel: false,
			})
		}
		else{
			uni.showModal({
				title: '',
				content: err.data.msg,
				showCancel: false,
			})
		}
	})	
}
function wstGetCode(code){
	try {
		window.croods.customPlugin({
			action: "UserPlugin.login",
			params: {},
			success: function(res) {
				let loginCount = localStorage.getItem("loginCount");
				if(code != "idass.account.LoginInvalid"){
					if(typeof(localStorage.getItem("loginCount"))!=undefined){
						if(loginCount == 0 || loginCount == "" || loginCount == null){
							localStorage.setItem("loginCount",1);
							wstLogin(res.token);
						}
					}
					else{
						wstLogin(res.token);
					}
				}
				else{
					uni.showModal({
						title: '',
						content: '',
						showCancel: false,
					})
				}
				// wstLogin(res.token);
			},
			fail: function(msg) {
				window.alert("")
			},
		})
	}
	catch(err){
		uni.hideLoading()
	}
}
function get(url, data = {}, header) {
	return request(url, data, 'GET', header);
}

function post(url, data = {}, header) {
	return request(url, data, 'POST', header);
}
